:root {
  --ff-inter: "Inter", sans-serif;
  --fs-h1: 2rem;
  --fs-h2: 1.5rem;
  --fs-h3: 1.25rem;
  --fs-body: 1rem;
  --fs-small: .875rem;
  --fs-xs: .75rem;
  --fw-bold: 700;
  --fw-semi-bold: 600;
  --fw-medium: 500;
  --fw-regular: 400;
}

@media (min-width: 960px) {
  :root {
    --fs-h1: 2.5rem;
    --fs-h2: 2rem;
    --fs-h3: 1.5rem;
    --fs-body: 1.125rem;
    --fs-small: 1rem;
    --fs-xs: .875rem;
  }
}
:root {
  --clr-black: 0, 0%, 0%;
  --clr-white: 0, 0%, 100%;
  --clr-gray: 0, 0%, 50%;
  --clr-gray-light: 240, 1%, 83%;
  --clr-primary: 90, 75%, 31%;
  --clr-primary-light: 100, 50%, 95%;
  --clr-primary-dark: 98, 39%, 12%;
  --clr-secondary: 255, 5%, 16%;
  --clr-secondary-light: 307, 6%, 30%;
  --clr-accent: 18, 26%, 50%;
}

.temp2 {
  --clr-black: hsl(0, 0%, 0%);
  --clr-white: hsl(0, 0%, 100%);
  --clr-gray: hsl(0, 0%, 50%);
  --clr-gray-light: hsl(240, 1%, 83%);
  --clr-primary: hsl(90, 75%, 31%);
  --clr-primary-light: hsl(100, 50%, 95%);
  --clr-primary-dark: hsl(98, 39%, 12%);
  --clr-secondary: hsl(255, 5%, 16%);
  --clr-secondary-light: hsl(307, 6%, 30%);
  --clr-accent: hsl(18, 26%, 50%);
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: var(--ff-inter);
  font-size: var(--fs-body);
  font-weight: var(--fw-regular);
  color: hsl(var(--clr-secondary));
  line-height: 1.6;
}

img {
  display: block;
  max-width: 100%;
}

h1, h2, h3, h4 {
  margin-top: 0;
  font-weight: var(--fw-bold);
}

h1 {
  font-size: var(--fs-h1);
}

h2 {
  font-size: var(--fs-h2);
}

h3 {
  font-size: var(--fs-h3);
}

h4 {
