.menu-btn {
  display: none;
  position: relative;
  height: 26px;
  width: 26px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
  z-index: 10;
}
.menu-btn .burger {
  position: absolute;
  width: 100%;
  height: 4px;
  top: 10px;
  border-radius: 1em;
  background-color: hsla(var(--clr-secondary), 0.9);
  transition: all 400ms ease-in-out;
}
.menu-btn .burger::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  top: -8px;
  border-radius: 1em;
  background-color: hsla(var(--clr-secondary), 0.9);
  transition: all 400ms ease-in-out;
}
.menu-btn .burger::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  left: 0;
  top: 8px;
  border-radius: 1em;
  background-color: hsla(var(--clr-secondary), 0.9);
  transition: all 400ms ease-in-out;
}
.menu-btn.active {
  width: 26px;
}
.menu-btn.active .burger {
  background-color: transparent;
}
.menu-btn.active .burger::before {
  transform: translateY(200%) rotate(45deg);
}
.menu-btn.active .burger::after {
  transform: translateY(-200%) rotate(-45deg);
}

