@use '../utils/' as u;

.contact {
    padding: 6em 0 10em;

    .container {
        @include u.container;
        position: relative;

        &::before {
            content: 'Contact';
            position: absolute;
            top: -1.4em;
            left: 0;
            font-size: 4em;
            color: hsla(var(--clr-black), .1);
        }
    }

    @include u.breakpoint-up(md) {
        &__actions {
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 3em;
        }
    }

    @include u.breakpoint-up(lg) {
        &__actions {
            gap: 6em;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        // background-color: hsl(var(--clr-white));
        // border: 1px solid hsl(var(--clr-black));
        // padding: 2em;
        margin-bottom: 2em;

        & > * {
            margin-bottom: 1em;
            font-family: var(--ff-inter);
            font-size: var(--fs-body);
            padding: .5em;
            border: none;
            border-radius: .25em;
            color: hsl(var(--clr-white));
            background-color: hsl(var(--clr-secondary));
        }

        .submit-btn {
            color: hsl(var(--clr-white));
            background-color: hsl(var(--clr-primary)); 
            border: 1px solid hsl(var(--clr-primary));          
            padding: .625em 1.5em;
            margin-bottom: 2em;
            cursor: pointer;
            transition: all 400ms ease-in-out;
            align-self: flex-start;

            &:hover {
                // color: hsl(var(--clr-secondary));
                // background-color: hsla(var(--clr-primary), .75); 

                background-color: hsl(var(--clr-white));
                color: hsl(var(--clr-secondary));
                border: 1px solid hsl(var(--clr-gray-light));   
                box-shadow: -4px 4px 7px -2px hsla(var(--clr-black), .5);
            }
        }
    }

    // &__links {
    //     border: 1px solid firebrick;
    // }

    span {
        display: flex;
        align-items: center;
        margin-bottom: 2em;
    }

    &__icon {
        margin-right: 1em;
    }

    &__option {
        // margin-top: 2em;
        font-size: var(--fs-h3);
        font-weight: var(--fw-bold);
        position: relative;
        margin-bottom: 2em;
        display: inline-block;

        &::before {
            content: url(../images/arrow.svg);
            position: absolute;
            top: 40%;
            right: -100%;
            // transform: scale(.75) rotate(15deg);
        }
    }

    .social__icons {
        margin-bottom: 2em;

        .social__links {
            margin-right: 1.5em;

            &:last-child {
                margin-right: unset;
            }
        }

        .social__icon {
            color: hsl(var(--clr-black));
            font-size: 1.25em;
            width: 1em;
            height: 1em;
            // background-color: hsla(var(--clr-black), .135);
            border-radius: 50%;
            border: 1px solid hsla(var(--clr-black), .25);
            padding: .5em;
            transition: all 400ms ease-in-out;

            &:hover {
                color: hsl(var(--clr-white));
                background-color: hsla(var(--clr-primary), .75);
            }
        }
    }

}